  components: {
    applyApprovebox,
    'date-picker': myDatepicker
  },
  computed: {},
  mounted() {},
  methods: {
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.realList = to.meta.routerList
      });
    },
    initPage() {
      //判断是什么类型type=>创建类型 id=>修改的时候的approval_id aType=>审批类型 1.出勤，2.用印 3.签字
      if (!this.$route.query.approval_id) {
        ///新建审批
        this.createType = 0;
      } else {
        //修改||重新发起审批
        this.approval_id = this.$route.query.approval_id;
        //获取发起类型
        if (!this.$route.query.type) {
          this.$router.push({
            name: ''
          })
        } else {
          this.createType = this.$route.query.type
        }
      }
      //判断来源
      if (!this.$route.query.from) {
        this.from = '';
        this.$router.push({
          name: 'investProjectManageList'
        })
      } else {
        this.from = this.$route.query.from;
      }
      if (this.from == 2) {
        if (!this.$route.query.projectId) {
          this.projectId = '';
          this.$router.push({
            name: 'investProjectManageList'
          })
        } else {
          this.projectId = this.$route.query.projectId;
        }
      }
      //获取
      if (this.createType == 0) {
        if (!this.$route.query.template_id || !this.$route.query.aName || !this.$route.query.aType) {
          if (this.from == 2) {
            this.$router.push({
              name: 'investProjectManageList'
            })
          } else if (this.from == 3) {
            this.$router.push({
              name: 'administrativeManageHomepageNew'
            })
          }
        } else {
          this.applyType = this.$route.query.aType;
          this.aName = this.$route.query.aName;
          this.template_id = this.$route.query.template_id;
        }
      } else {
        if (!this.$route.query.aType) {
          if (this.from == 2) {
            this.$router.push({
              name: 'investProjectManageList'
            })
          } else if (this.from == 3) {
            this.$router.push({
              name: 'administrativeManageHomepageNew'
            })
          }
        } else {
          this.applyType = this.$route.query.aType;
        }
      }
      if (this.applyType == 2) {
        this.applyTypeName = '用印'
      } else if (this.applyType == 1) {
        this.applyTypeName = '出勤'
      } else if (this.applyType == 3) {
        this.applyTypeName = '签字'
      }
      this.initData();
    },
    //初始化页面
    initData() {
      this.getCreateApplyFun();
    },
    //获取城市/行业
    getCityOrIndustry(item) {
      if (item.value_map.hasGetdata == false) {
        if (item.value_map.type == 1) {
          getCityArea('').then(res => {
            if (res.data.msgNo == 1) {
              item.value_map.total_list = res.data.payload;
              item.value_map.search_list = res.data.payload;
              item.value_map.hasGetdata = true;
              if (item.value_map.value == undefined || item.value_map.value == null || item.value_map.value == '') {
                item.value_map.value = [];
              } else {
                let city = [];
                item.value_map.value.forEach(item => {
                  city.push(item.id)
                })
                item.value_map.value = city;
              }
            }
          })
        } else {
          industryCategory('').then(res => {
            if (res.data.msgNo == 1) {
              item.value_map.total_list = res.data.payload;
              item.value_map.search_list = res.data.payload;
              item.value_map.hasGetdata = true;
              if (item.value_map.value == undefined || item.value_map.value == null || item.value_map.value == '') {
                item.value_map.value = [];
              } else {
                let city = [];
                item.value_map.value.forEach(item => {
                  city.push(item.id)
                })
                item.value_map.value = city;
              }
            }
          })
        }
      }
    },
    //前往请假记录列表
    gotoApplylist() {
      let newObj = {};
      newObj = this.$route.query
      newObj.status = 3;
      this.$router.push({
        name: 'applyListNew',
        query: newObj
      })
    },
    //显示我的假期弹窗
    showMyvacationClikc() {
      this.showMyvacation = true;
      if (this.myVacation.length == 0) {
        //获取假期
        showVacation('').then(res => {
          if (res.data.msgNo == 1) {
            this.myVacation = res.data.payload;
          }
        })
      }
    },
    //修改时间范围计算
    changeRangetime(e) {
      if (e.value_map.value != null && e.value_map.value.length == 2) {
        let start_time = e.value_map.value[0];
        let end_time = e.value_map.value[1];
        calcTotalTime({
          start_time: start_time / 1000,
          end_time: end_time / 1000,
          type: e.value_map.type
        }).then(res => {
          if (res.data.msgNo == 1) {
            for (let item in this.applyShowlist) {
              if (this.applyShowlist[item].fields == e.value_map.relate) {
                this.applyShowlist[item].value_map.value = res.data.payload;
                break;
              }
            }
          }
        })
      }
    },
    changeRangetime1(e) {
      let startTime = e.value_map.startTime.time;
      let endTime = e.value_map.endTime.time;
      if (startTime != '' && endTime != '') {
        let start_time = Date.parse(new Date(startTime));
        let end_time = Date.parse(new Date(endTime));
        e.value_map.value = [];
        e.value_map.value.push(start_time);
        e.value_map.value.push(end_time);
        calcTotalTime({
          start_time: start_time / 1000,
          end_time: end_time / 1000,
          type: e.value_map.type
        }).then(res => {
          if (res.data.msgNo == 1) {
            for (let item in this.applyShowlist) {
              if (this.applyShowlist[item].fields == e.value_map.relate) {
                this.applyShowlist[item].value_map.value = res.data.payload;
                break;
              }
            }
          }
        })
      }
    },
    //获取组织架构人员
    getUserCompanyFun() {
      getUserCompany({}).then(res => {
        if (res.data.msgNo == 1) {
          this.cs_list = [];
          this.select_cs_list = [];
          let cl = [];
          let pl = res.data.payload;
          pl.forEach(item => {
            if (item.children != undefined) {
              cl = cl.concat(item.children);
            }
          })
          cl.forEach(i => {
            if (this.cs_default.indexOf(i.id.toString()) != -1) {
              i.is_default = true;
              i.is_select = 1;
            } else {
              i.is_default = false;
              i.is_select = 0;
            }
          })
          this.cs_list = cl;
          this.getLeaveInfoFun();
        }
      })
    },
    //获取抄送和审批人
    getLeaveInfoFun() {
      let param = {
        template_id: this.template_id,
        sid: this.approval_id,
        fund_id: this.fund_id,
        project_id: this.project_id
      };
      getLeaveInfo(param).then(res => {
        this.sp_list = res.data.payload.sp;
        this.cs_default = [];
        this.select_cs_list = [];
        let search_list = [];
        let cs_sellist = [];
        res.data.payload.cs.forEach(item => {
          cs_sellist.push(item.uid);
        })
        if (res.data.payload.default != null && res.data.payload.default != '') {
          this.cs_default = res.data.payload.default.toString().split(',');
        }
        this.cs_list.forEach(i => {
          if (this.cs_default.indexOf(i.id.toString()) != -1) {
            i.is_default = true;
          } else {
            i.is_default = false;
          }
          if (cs_sellist.indexOf(i.id) != -1) {
            i.is_select = 1;
            this.select_cs_list.push(i);
          } else {
            i.is_select = 0;
          }
          //search_list.push(i);
        });
        this.searchlistdata = {
          search_list: search_list,
          searchText: '', //输入框内内容
          onfocus: -1, //当前下拉列表选中项
          showlist: false, //是否显示搜索下拉框
          scroll_top: 0, //搜索下拉框滚动条距离上面距离
        }
      })
    },
    //获取用印模板
    getCreateApplyFun() {
      var that = this;
      getCreateApply({
        template_id: this.template_id,
        sid: this.approval_id //404
      }).then(res => {
        let as = res.data.payload;
        if (res.data.msgNo == 1) {
          as = res.data.payload;
          //this.info = res.data.payload.info;
          as.forEach(item => {
            //基金选择
            if (item.control == 1) {
              let vlist = item.value_list;
              item.selected_value = '';
              vlist.forEach(item11 => {
                if (item11.is_select == 1) {
                  item.selected_value = item11.id;
                }
              })
            } else if (item.control == 2) {
              let search_list = [];
              item.search_list = [];
              item.hasGetdata = false;
              item.selected_value = '';
              if (item.value_map.name != null) {
                item.selected_value = item.value_map.name;
              }
            } else if (item.control == 6) { //印章选择
              let selectlist = item.value_list;
              selectlist.forEach(item1 => {
                item1.options = [];
                for (let i = 0; i < 51; i++) {
                  item1.options.push(i); //select选择框
                };
                //item1.checked = false;
                if (item1.count > 0) {
                  item1.checked = true;
                } else {
                  item1.checked = false
                }
                item1.showlist = false;
              });
              item.selectlistdata = {
                select_list: selectlist
              }
            } else if (item.control == 5) { //是否发短信
              let radioList = [{
                value: 1,
                name: '是'
              }, {
                value: 0,
                name: '否'
              }];
              let radioList_checked = item.value_map.is_select;
              if (radioList_checked == null) {
                radioList_checked = 0;
              }
              item.radiolistdata = {
                checked: radioList_checked,
                radio_list: radioList
              }
              if (item.value_map.hide != undefined) {
                item.radiolistdata.hasHide = true;
                item.radiolistdata.hidelist = item.value_map.hide.split(",");
                as.forEach(item1 => {
                  if (item.radiolistdata.hidelist.indexOf(item1.fields) != -1) {
                    item1.is_show = radioList_checked
                  } else {
                    item1.is_show = 1
                  }
                })
              } else {
                item.radiolistdata.hasHide = false
              }
            } else if (item.control == 9 || item.control == 8) { //附件
              item.filelistdata = {
                file_list: item.value_list
              }
            } else if (item.control == 10) {
              item.value_map.total_list = [];
              item.value_map.hasGetdata = false;
              item.value_map.search_list = [];
              if (item.value_map.multi == 0) {
                item.value_map.multiple_limit = 1;
              } else {
                item.value_map.multiple_limit = 0;
              }
              that.getCityOrIndustry(item);
            } else if (item.control == 11) {
              item.name = item.name.toString().split(",");
              if (item.value_map.value == null || item.value_map.value == undefined) {
                item.value_map.value = [];
                item.value_map.startTime = {
                  time: ''
                }
                item.value_map.endTime = {
                  time: ''
                }
              } else {
                item.value_map.value = item.value_map.value.toString().split(",");
                /*let valuelist = [];
                item.value_map.value.forEach(item => {
                  let num = parseInt(item) * 1000;
                  valuelist.push(num);
                })*/
                if (item.value_map.type == 1) {
                  item.value_map.startTime = {
                    time: new Date(parseInt(item.value_map.value[0]) * 1000).Format('yyyy-MM-dd')
                  }
                  item.value_map.endTime = {
                    time: new Date(parseInt(item.value_map.value[1]) * 1000).Format('yyyy-MM-dd')
                  }
                } else {
                  item.value_map.startTime = {
                    time: new Date(parseInt(item.value_map.value[0]) * 1000).Format('yyyy-MM-dd hh:mm')
                  }
                  item.value_map.endTime = {
                    time: new Date(parseInt(item.value_map.value[1]) * 1000).Format('yyyy-MM-dd hh:mm')
                  }
                }

                //item.value_map.value = valuelist;
              }
            } else if (item.control == 13) {
              if (item.value_map.value == null || item.value_map.value == undefined) {
                item.value_map.value = '';
              }
            }
            if (item.is_must == 1) {
              item.showis_must = 0;
            } else {
              item.showis_must = -1;
            }
            if (item.is_show == undefined) {
              item.is_show = 1;
            }
          });
          this.applyShowlist = as;
          this.getUserCompanyFun();
        }
      })
    },
    //获取项目/基金列表
    getProjectlist(item) {
      if (item.hasGetdata == false) {
        getFundOrProject({
          page: 1,
          pageSize: 1000,
          type: item.value_map.type
        }).then(res => {
          if (res.data.msgNo == 1) {
            item.search_list = res.data.payload;
            item.hasGetdata = true;
          }
        })
      }
    },
    //修改项目/基金
    changeproject(id, type) {
      if (type == 1) {
        this.fund_id = id
      } else if (type == 2) {
        this.project_id = id
      }
      this.getLeaveInfoFun();
    },
    //点击页面其他位置关闭下拉框
    closedropdownlist() {
      this.searchlistdata.showlist = false;
      if (this.$refs.searchboxDropdownlist != undefined) {
        if (this.$refs.searchboxDropdownlist.length > 0) {
          this.$refs.searchboxDropdownlist.forEach(item => {
            this.applyShowlist[item.attributes.index.value].searchlistdata.showlist = false;
          })
        }
      }
      if (this.$refs.inputselectboxdropdownbox != undefined) {
        if (this.$refs.inputselectboxdropdownbox.length > 0) {
          this.$refs.inputselectboxdropdownbox.forEach(item1 => {
            this.applyShowlist[item1.attributes.index.value].selectlistdata.select_list.forEach(item2 => {
              item2.showlist = false;
            });
          })
        }
      }
    },
    //修改印章数量
    changeseal(i, j) {
      if (j == 0) {
        i.checked = false
      }
    },
    //修改用印类型
    changeTemplate() {
      this.project_id = '';
      this.fund_id = '';
      this.getLeaveInfoFun();
      this.getCreateApplyFun();
    },
    //搜索框显示下拉
    showlist() {
      this.searchlistdata.showlist = true;
    },
    //实时搜索
    searchlistFunc(e) {
      if (e.keyCode != 40 && e.keyCode != 38) {
        let searchList = this.cs_list;
        this.searchlistdata.search_list = [];
        searchList.forEach(item => {
          if (item.is_select == 0) {
            if (e.target.value == '' || item.name.indexOf(e.target.value) > -1) {
              this.searchlistdata.search_list.push(item);
            }
          }
        })
      }
    },
    //上下按键选择下拉选项
    changeSearchlistFocus(type) {
      let sld = this.searchlistdata;
      if (type == "up") {
        if (sld.onfocus > 0) {
          sld.onfocus--;
        } else {}
      } else {
        if (sld.onfocus < sld.search_list.length - 1) {
          sld.onfocus++;
        }
      }
      sld.scroll_top = 40 * sld.onfocus;
      sld.searchText = sld.search_list[sld.onfocus].name;
      this.searchlistdata.searchlistdata = sld;
    },
    //下拉菜单显示隐藏
    dropdowntoggle(check, i, j, val) {
      if (check) {
        this.applyShowlist[i].selectlistdata.select_list[j].showlist = val;
      }
    },
    //上传文件
    uploadfile(index, e) {
      let file = [];
      for (let i = 0; i < e.target.files.length; i++) {
        file.push(e.target.files[i]);
      }
      uploadMoreApprove({
        control: this.applyShowlist[index].control,
        template_id: this.template_id
      }, file).then(res => {
        if (res.data.msgNo == 1) {
          res.data.payload.forEach(item => {
            this.applyShowlist[index].filelistdata.file_list.push(item);
          })
        }
      })
      e.target.value = ''
    },
    //拖拽上传文件
    uploadfileRequest($event) {
      let index = $event.data.index;
      let file = [];
      if ($event.file.type == "") {
        try {
            var fileReader = new FileReader();
            fileReader.readAsDataURL(dropFile.slice(0, 3));
            fileReader.addEventListener('load', function (e) {
              console.log(e, 'load');
              file.push($event.file);
            }, false);
            fileReader.addEventListener('error', function (e) {
              dialogConfirm('info', '不支持该类型文件上传');
            }, false);

        } catch (e) {
          dialogConfirm('info', '不支持该类型文件上传');
        }
        return;
      } else {
        file.push($event.file);
      }
      uploadMoreApprove({
        control: this.applyShowlist[index].control,
        template_id: this.template_id
      }, file).then(res => {
        if (res.data.msgNo == 1) {
          res.data.payload.forEach(item => {
            this.applyShowlist[index].filelistdata.file_list.push(item);
          })
        }
      })
    },
    //替换文件
    changefile(pindex, index, e) {
      this.uploadfile(pindex, e);
      this.deletefile(pindex, index);
    },
    //搜索选择
    addSelectedItem(index) {
      let id = this.searchlistdata.search_list[index].id;
      this.searchlistdata.search_list.splice(index, 1);
      this.cs_list.forEach(item => {
        if (item.id == id) {
          item.is_select = 1;
          this.select_cs_list.push(item);
        }
      });
    },
    //印章选择,选中改为1，未选中改为0；
    sealChecked(pindex, index) {
      let current = this.applyShowlist[pindex].selectlistdata.select_list[index];
      if (current.checked) {
        current.count = 1;
      } else {
        current.count = 0;
      }
    },
    //删除已选中的多选项
    deleteSelectedItem(item, index) {
      item.is_select = 0;
      this.select_cs_list.splice(index, 1)
    },
    //删除已选文件
    deletefile(pindex, index) {
      let imgpath = this.applyShowlist[pindex].filelistdata.file_list[index].url;
      this.delFilelist.push(imgpath);
      this.applyShowlist[pindex].filelistdata.file_list.splice(index, 1);
      if (this.createType == 0) {
        this.deletefileFun();
      }
    },
    //彻底删除文件
    deletefileFun() {
      this.delFilelist.forEach(item => {
        deleteFileUrl({
          path: encodeURIComponent(item)
        }).then(res => {})
      })
      this.delFilelist = [];
    },
    changeRadioshow(i) {
      if (i.radiolistdata.hasHide) {
        this.applyShowlist.forEach(item => {
          if (i.radiolistdata.hidelist.indexOf(item.fields) != -1) {
            item.is_show = i.radiolistdata.checked;
          }
        })
      }
    },
    //提交按钮
    submitApply() {
      let data = [];
      let submitOk = true; //验证界面，是否可提交
      this.applyShowlist.forEach(item => {
        //基金选择
        if (item.is_show == 1) {
          if (item.control == 1) {
            if (item.is_must == 1 && item.selected_value == '') {
              item.showis_must = 1;
              submitOk = false;
            } else {
              item.showis_must = 0;
              data[item.fields] = item.selected_value;
            }
          } else if (item.control == 2) {
            if (item.is_must == 1 && item.selected_value == '') {
              item.showis_must = 1;
              submitOk = false;
            } else {
              item.showis_must = 0;
              if (item.selected_value === item.value_map.name) {
                data[item.fields] = item.value_map.id
              } else {
                data[item.fields] = item.selected_value;
              }
            }
          } else if (item.control == 4 || item.control == 3) {
            if (item.is_must == 1 && (item.value == null || item.value == '')) {
              item.showis_must = 1;
              submitOk = false;
            } else {
              item.showis_must = 0;
              data[item.fields] = item.value;
            }
          } else if (item.control == 5) { //是否发短信
            let radioList = item.radiolistdata.checked;
            data[item.fields] = radioList;
          } else if (item.control == 6) { //印章选择
            let selectedCheck = [];
            item.selectlistdata.select_list.forEach(item1 => {
              if (item1.checked) {
                let as = {
                  id: item1.id,
                  name: item1.name,
                  count: item1.count
                }
                selectedCheck.push(as)
              }
            });
            if (item.is_must == 1 && selectedCheck.length == 0) {
              item.showis_must = 1;
              submitOk = false;
            } else {
              item.showis_must = 0;
              data[item.fields] = JSON.stringify(selectedCheck);
            }
          } else if (item.control == 9 || item.control == 8) {
            let filelist = item.filelistdata.file_list;
            if (item.is_must == 1 && filelist.length == 0) {
              item.showis_must = 1;
              submitOk = false;
            } else {
              item.showis_must = 0;
              data[item.fields] = JSON.stringify(filelist);
            }
          } else if (item.control == 10) {
            if (item.is_must == 1 && item.value_map.value == 0) {
              submitOk = false;
              item.showis_must = 1;
            } else {
              item.showis_must = 0;
              data[item.fields] = item.value_map.value.toString();
            }
          } else if (item.control == 11) {
            if (item.is_must == 1 && item.value_map.value.length == 0) {
              submitOk = false;
              item.showis_must = 1;
            } else {
              item.showis_must = 0;
              let timelist = [];
              if (item.value_map.value.length != 0) {
                item.value_map.value.forEach(item111 => {
                  timelist.push(item111 / 1000);
                })
              }
              data[item.fields] = timelist.toString();
            }
          } else if (item.control == 13) {
            if (item.is_must == 1 && (item.value_map.value == null || item.value_map == '')) {
              submitOk = false;
              item.showis_must = 1;
            } else {
              data[item.fields] = item.value_map.value;
            }
          }
        }
      });
      if (this.approval_id != '') {
        data.approval_id = this.approval_id;
      };
      data.template_id = this.template_id;
      let cs_selected = [];
      this.cs_list.forEach(item => {
        if (item.is_select == 1) {
          cs_selected.push(item.id);
        }
      })
      data.copier = cs_selected.toString();
      let newObj = this.$route.query;
      newObj.status = 3;
      if (submitOk) {
        if (this.createType == 0) {
          submitApprove(data).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '提交成功');
              /*if (this.isSr) {
                newObj = { template_id: this.$route.query.ctid, aType: this.$route.query.aType, aName: this.$route.query.aName, id: 3 }
              } else {
                newObj = { id: 3 }
              }*/
              this.$router.push({
                name: 'applyListNew',
                query: newObj
              })
            } else {
              dialogConfirm('fail', res.data.message);
            }
          })
        } else if (this.createType == 2) {
          updateApprove(data).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '提交成功');
              /*if (this.isSr) {
                newObj = { template_id: this.$route.query.ctid, aType: this.$route.query.aType, aName: this.$route.query.aName, id: 3 }
              } else {
                newObj = { id: 3 }
              }*/
              this.$router.push({
                name: 'applyListNew',
                query: newObj
              })
            } else {
              dialogConfirm('fail', res.data.message);
            }
          })
        } else if (this.createType == 1) {
          if (this.applyType == 1 || this.applyType == 3) {
            editLeave(data).then(res => {
              if (res.data.msgNo == 1) {
                dialogConfirm('success', '提交成功');
                /*if (this.isSr) {
                  newObj = { template_id: this.$route.query.ctid, aType: this.$route.query.aType, aName: this.$route.query.aName, id: 3 }
                } else {
                  newObj = { id: 3 }
                }*/
                this.$router.push({
                  name: 'applyListNew',
                  query: newObj
                })
              } else {
                dialogConfirm('fail', res.data.message);
              }
            })
          } else if (this.applyType == 2) {
            editApprove(data).then(res => {
              if (res.data.msgNo == 1) {
                dialogConfirm('success', '提交成功');
                /*if (this.isSr) {
                  newObj = { template_id: this.$route.query.ctid, aType: this.$route.query.aType, aName: this.$route.query.aName, id: 3 }
                } else {
                  newObj = { id: 3 }
                }*/
                this.$router.push({
                  name: 'applyListNew',
                  query: newObj
                })
              } else {
                dialogConfirm('fail', res.data.message);
              }
            })
          }

        }
        this.deletefileFun();
      } else {
        dialogConfirm('info', '您还有必填项没有填写');
      }
    },
    //图片获取失败
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.table-group-title .breadcrumb {
  margin-top: 20px;
}

.search-box .search-box-input {
  height: 40px;
}

.search-box .search-box-input:focus {
  background: #fff;
}

.search-box .search-box-icon {
  top: 12px;
}

.formgroup-file-item-btn {
  position: relative;
  overflow: hidden;
}

.formgroup-file-item-btn-inputfile {
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  opacity: 0;
  cursor: pointer;
}

</style>



